import { Component } from '@angular/core';
@Component({
    selector: 'app-sign-in',
    template: `
    	<form (ngSubmit)="onSubmit(formSignIn);" #formSignIn="ngForm">
            <input ngModel placeholder="Email" #txtEmail="ngModel" name="email" required email><br>
            <p style="color:red" *ngIf="txtEmail.touched && txtEmail.errors?.required">Email is requred</p>
            <p style="color:red" *ngIf="txtEmail.touched && txtEmail.errors?.email">Email is invalid</p>
            <input ngModel type="password" name="password" required placeholder="Password"><br><br>
            <button [disabled]="formSignIn.invalid">Submit</button>
        </form>
        <p>{{ txtEmail.errors | json }}</p>        
    `,
    styleUrls: ['./app.component.css']
})

export class SignInComponent {
	onSubmit(formSignIn) {
		console.log(formSignIn);
		throw new Error('Form is invalid');
	}
}


